<template>
<div class="wrapper">
  <swiper :options="swiperOption">
    <swiper-slide
    v-for="img of list"
    :key="img.id">
      <img
      class="swiper-img"
      :src="img.imgUrl"
      alt="">
    </swiper-slide>
    <div class="swiper-pagination"  slot="pagination"></div>
  </swiper>
</div>
</template>
<script>
export default {
  name: 'HomeSwiper',
  props: {
    list: Array
  },
  data () {
    return {
      swiperOption: {
        pagination: '.swiper-pagination',
        loop: true
      }
    }
  }
}
</script>
<style lang="stylus" scoped>
.wrapper >>> .swiper-pagination-bullet-active
  background: #fff !important
.wrapper
  overflow: hidden
  width: 100%
  height: 0
  padding-bottom: 31.66666%
  background: #eee
  .swiper-img
    width: 100%
</style>



