import axios from 'axios'

const baseURL = 'https://www.easy-mock.com/mock/5bfd385f74cdf85be995b3b5/travel/'

axios.defaults.baseURL = baseURL

export const getIndex = params => {
  return axios.get('index', {params: params}).then(res => res.data)
}

export const getCity = params => {
  return axios.get('city', params).then(res => res.data)
}



